import React from 'react'
const sidebarStyle = {
    position: 'fixed',
    left: '0',
    top: '0',
    bottom: '0',
    height: '100vh',
    width: '200px',
    backgroundColor: '#f2f2f2',
    zIndex: 1,
}
export default function Sidebar({children}) {
  return (
     <div className="container" style={sidebarStyle}>{children}</div>
  )
}
