import React from 'react'

const style = {
    left: '0',
    top: '0',
    bottom: '0',
    position: "fixed",
    backgroundColor: 'black',
    height: '10%',
    width: '100%',
    zIndex: 2,
}

export default function NavBar({children}) {
  return (
    <div style={ style }>
      {children}
    </div>
  )
}
